.card-deck {
    margin: 5vh auto;
    display: grid;
    grid-gap: var(--card-spacing);
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: 1fr;
}

.card {
    position: relative;
    width: 100%;
    aspect-ratio: 1;
    perspective: 400px;
    perspective-origin: 50% 50%;
}

.card:hover {
    z-index: 1;
}

.card-bg {
    width: 100%;
    aspect-ratio: 1;
    background-size: 100%, 100%;
    border-radius: 5px;
    transition: transform 200ms ease-in-out;
    transform-style: preserve-3d;
    box-shadow: 5px 5px 5px #010101aa;
    overflow: hidden;
}

.sm.card:hover .card-bg {
    transform: rotateX(8deg) translateZ(30px);
    box-shadow: 5px 5px 5px #010101aa;
}

.md.card:hover .card-bg {
    transform: rotateX(6deg) translateZ(25px);
    box-shadow: 5px 5px 5px #010101aa;
}

.lg.card:hover .card-bg {
    transform: rotateX(4deg) translateZ(20px);
    box-shadow: 5px 5px 5px #010101aa;
}

.card-overlay {
    width: 100%;
    aspect-ratio: 1;
    mix-blend-mode: lighten;
    transition: background-position 200ms ease-in-out;
    background-image: linear-gradient(10deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.04) 46%, rgba(255,255,255,0.10) 65%, rgba(255,255,255,0.17) 72%, rgba(255,255,255,0.064) 100%);
    background-size: 100% 150%;
    background-position: bottom;
}

.card:hover .card-overlay {
    background-position: 0;
}

.card-header {
    position: absolute;
    padding: .4em .6em;
    margin: 0;
    bottom: 0; 
    left: 0;
    border-radius: 0 5px 0 0;
    background-color: rgba(0, 0, 0, .6);
    transition: opacity 200ms ease-in-out;
    opacity: 0;
}

.card:hover .card-header {
    opacity: 1;
}