:root {
    --card-spacing: 1.5vh;
    --sm: 1.25;
    --md: 1.65;
    --lg: 2;
}

body {
    margin: 0;
    background-color: #121212;
}

.section-header {
    margin: 0 auto;
    color:white;
    font-family: 'Montserrat', sans-serif;
    font-size: 4vw;
    width: max-content;
    margin-bottom: 0;
}

.header-underline {
    height: 6px;
    animation:  background-color-cycle 10s linear 0s infinite normal none,
                shadow-color-cycle 10s linear 0s infinite normal none;
}

.text-header {
    margin-bottom: 0;
    font-family: 'Montserrat', sans-serif;
    color:white;
}

.text-body {
    color:white;
    font-family: 'Roboto', sans-serif;
}

.no-link {
    cursor: not-allowed;
}

#bg-anim {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: -1;
}

@keyframes background-color-cycle {
    0%   {background-color: #fcc;}
    16%  {background-color: #ffc;}
    33%  {background-color: #cfc;}
    50%  {background-color: #cff;}
    66%  {background-color: #ccf;}
    87%  {background-color: #fcf;}
    100% {background-color: #fcc;}
}

@keyframes shadow-color-cycle {
    0%   {box-shadow: 0 0 20px #f00;}
    16%  {box-shadow: 0 0 25px #ff0;}
    33%  {box-shadow: 0 0 20px #0f0;}
    50%  {box-shadow: 0 0 25px #0ff;}
    66%  {box-shadow: 0 0 20px #00f;}
    87%  {box-shadow: 0 0 25px #f0f;}
    100% {box-shadow: 0 0 20px #f00;}
}

@keyframes text-color-cycle {
    0%   {color: #faa;}
    16%  {color: #ffa;}
    33%  {color: #afa;}
    50%  {color: #aff;}
    66%  {color: #aaf;}
    87%  {color: #faf;}
    100% {color: #faa;}
}

/* mobile */
@media all and (max-width: 767.9px) {
    .content-container {
        width: 100%;
    }

    .card-deck {
        width: 250px;
        display: flex;
        flex-direction: column;   
    }

    .card {
        margin-top: 30px;
    }

    .section-header {
        font-size: 30px;
    }

    .sm .card-header {
        font-size: 20px;
    }

    .md .card-header {
        font-size: 20px;
    }

    .lg .card-header {
        font-size: 20px;
    }

    .scale-text-body {
        font-size: 16px;
    }

    .splash-container {
        height: 100vh;
    }

    .splash-text-container {
        top: 40%;
    }

    .splash-text-container h1 {
        font-size: 10vw;
    }

    .splash-text-container h2 {
        font-size: 4.5vw;
    }
}

/* larger screens */
@media all and (min-width: 768px) {
    .content-container {
        width: 80%;
    }

    .card-deck {
        width: 400px;
    }
    
    .section-header {
        font-size: 40px;
    }

    .sm .card-header {
        font-size: calc(var(--sm) * 10px);
    }

    .md .card-header {
        font-size: calc(var(--md) * 10px);
    }

    .lg .card-header {
        font-size: calc(var(--lg) * 10px);
    }

    .scale-text-body {
        font-size: 20px;
    }
}

@media all and (min-width: 1024px) {
    .content-container {
        width: 80%;
    }

    .card-deck {
        width: 600px;
    }

    .section-header {
        font-size: 60px;
    }

    .sm .card-header {
        font-size: calc(var(--sm) * 15px);
    }

    .md .card-header {
        font-size: calc(var(--md) * 15px);
    }

    .lg .card-header {
        font-size: calc(var(--lg) * 15px);
    }

    .scale-text-body {
        font-size: 25px;
    }
}